$color-pink: #c90035;
$color-blue: #3968cb;
$text-color-pink: #ea4d9d;
$color-gold: #b4ac85;

// Colors
$bg-color: #1370fb;
$grey: #d3d3d3;
$background-color: rgba(64, 64, 64, 1);
$text-color-black: #393b45;

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  background-color: $background-color;
  font-family:  "Share Tech", sans-serif;
  font-weight: 400;
}

.heading {
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  text-align: left;
  margin-bottom: 0.2rem;
}

input[type="text"]
{
  font-family: "Share Tech", sans-serif;
}

h5 {
  color: $grey;
}

.round {
  border-radius: 50%;
}

.cursive-heading {
  font-family: "Charm", sans-serif;
  font-size: 1rem;
  font-weight: 700;
}

span {
  font-size:12px;
}

.achievement {
  border-bottom: 0.1rem solid #111;
  padding-bottom: 0.2rem;
  margin-bottom: 0.5rem;
}

.line {
  font-size: 17px;
  font-family: "Charm", sans-serif;
  font-weight: 400;
  padding-left: 0.5rem;
  color: $text-color-black;
}

.content {
  font-size: 15px;
  font-family: "Share Tech", sans-serif;
  padding: 1px 5px;
  display: -webkit-box;
  display: -moz-flexbox;
  display: flex;
  
  .task {
    margin-left: 3px;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
  }

  .check {
    width: 0.7rem;
    height: 0.7rem;
    border: 1px solid #d3d3d3;
    -ms-flex-item-align: center;
    -ms-grid-row-align: center;
    align-self: center;
  }
}

.daily-manifest {
  
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 2rem;

  .card {
    width: 45rem;
    height: 51rem;
    padding: 20px;
    background-color: white;
    box-shadow: 0 3px 16px rgba(0, 0, 0, 0.16), 0 3px 16px rgba(0, 0, 0, 0.23);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;

    &__header {
      height: 4rem;
      background-color: white;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: start;
      -ms-flex-pack: start;
      justify-content: flex-start;
      flex-direction: row;
      padding: 0.5rem;
      
      &_title {
        flex-grow: 20;

        h4 {
          margin: 0;
        }

        h5 {
          margin: 2px 0;
        }
      }

      &_date {
        flex-grow: 1;
        border-bottom: 2px solid #ccc;

        .input {
          float: left;
          margin-top: 20px;

          input {
            border: 0;
            background: transparent;
          }

          &:first-child {
            width: 51%;
          }

          &:last-child {
            width: 49%;
          }
        }
      }
    }

    &__content {
      flex-basis: 45rem;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
      -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      min-height: 0;
      min-width: 0;
      padding-top: 20px;
      background: white;

      .note {
        padding: 0.5rem;
        -ms-flex-preferred-size: 10rem;
        flex-basis: 10rem;
        width: 50%;
      }

      .todo {
        background-color: white;
        display: flex;
        
        i {
          margin-right:10px;
        }

        &__tasks {
          width: 50%;
          // margin-bottom: 1.5rem;

          .heading {
            border-bottom: 1px solid #d3d3d3;
            font-size: 11px;
            color: #ccc;
            padding: 5px 0;
          }
          .content {
            padding: 1px 0;
            height: 25px;
            margin: 7px 0;
            border-bottom: 1px solid #d3d3d3;
            display: block;
            width: 95%;
            display: flex;
            
            * {
              float: left;
            }

            .check {
              margin-top: 3px;
            }

            .task {
              flex-grow: 2;
              input {
                width: 95%;
                border:0;
                background-color: transparent;
              }
            }
          }
        }
      }

      .macro-goals {
        flex-basis: 5rem;
        flex-direction: column;

        .heading {
          margin-bottom: 6px;

          h3, em{
            font-size:11px;
            color: #ccc;
          }

          h3 {
            margin: 0;
            display: inline-block;
            float:left;
          }

          em {
            display: inline-block;
            float:right;
          }
        }

        .macro__tasks {
          width: 100% !important;
          border: 1px solid #d3d3d3;

          .content {
            display: flex;
            padding-left:10px;
            width: 100% !important;
            
            .task {
              flex-grow: 2;
              input {
                width: 95%;
                background-color: transparent;
              }
            }

            &:last-child {
              margin-bottom:0;
              border: 0;
            }
          }
        }
      }

      .micro-tasks {
          .content {
            margin: 2px 0;
            padding: 3px 0;

            .last {
              border-left: 1px solid #d3d3d3;
              height: 28px;
              width: 30px;
              margin-top: -6px;
            }
            .task {
             
            }
          }
      }

      .reflections {
        i {
          color: #d3d3d3;
        }
      }
      

      .week {
        background-color: #fff;
        
        &__title {
          margin-bottom: 1.1rem;
        }
        
        &.week__schedule {
          
          .heading {
            color: #d3d3d3;
            display: flex;

            h3 {
              margin:0;
              flex-grow: 1;
            }

            em {
              flex-grow: 2;
              i {
                float: right;
                text-decoration: line-through;
                font-size: 20px;
                margin-right: 15px;
              }
            }
          }

          .content {
            // border-top: 1px solid #d3d3d3;
            border-bottom: 1px solid #d3d3d3;
            padding: 0px 0;


            &:last-child {
              border-top:0px;
            }

            .input-box {
              flex-grow: 3;
              border-right: 1px solid #d3d3d3;
              // padding-right: 10px;

              input {
                float:right;
                border:0;
                margin-bottom: 0.5px;
                margin-top: 0.5px;
                background-color: transparent;
                width: 100%;

                &:first-child {
                  border-bottom: 1px solid #d3d3d3;
                }
              }

            }

            .big-input {
              input {
                width: 40px;
                height: 40px;
                border: 0;
                background: transparent;
              }
            }

            .big-input-first{
              input {
                width: 30px;
                height: 40px;
                border: 0;
                background: transparent;
              }
            }


          }
        }
      }

    }

    &__footer {
      height: 2.5rem;
      display: flex;
      background-color: white;
      .url {
        margin-left: 5px;
      }
      .social {
        flex-grow: 5;
        // justify-content: flex-end;

        * {
          display: inline-block;
          float:right;
          margin-right: 10px;
        }
        
        i {
          margin-top: 2 px;
        }

        span {
          margin-left:-5px;
          margin-top: 2px;
        }
      }
    }
  }
}

@media all and (max-width: 500px) {
  .wrapper {
    .card {
      height: 100%;
      &__content {
        -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
        -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
        .note {
          width: 100%;
        }
        .overall-report {
          -webkit-box-ordinal-group: 2;
          -ms-flex-order: 1;
          order: 1;
        }
        .daily-log {
          &__notes {
            .date {
              margin-left: 0.8rem;
            }
          }
        }
      }
    }
  }
}
